'use strict';

/**
* Test if a value is an `ArrayBuffer` view.
*
* @module @stdlib/assert/is-arraybuffer-view
*
* @example
* import isArrayBufferView from '@stdlib/assert/is-arraybuffer-view';
* import Int32Array from '@stdlib/array/int32';
*
* var bool = isArrayBufferView( new Int32Array( 10 ) );
* // returns true
*
* bool = isArrayBufferView( {} );
* // returns false
*/

// MODULES //

import hasArrayBufferSupport from './../../has-arraybuffer-support';
import isFunction from './../../is-function';
import ArrayBuffer from '@stdlib/array/buffer';
import noArraybuffer from './no_arraybuffer.js';
import polyfill from './polyfill.js';
import main from './main.js';


// MAIN //

var isArrayBufferView;
if ( hasArrayBufferSupport() ) {
	if ( isFunction( ArrayBuffer.isView ) ) {
		isArrayBufferView = main;
	} else {
		isArrayBufferView = polyfill;
	}
} else {
	isArrayBufferView = noArraybuffer;
}


// EXPORTS //

export default isArrayBufferView;
