'use strict';

/**
* Test if a value is a square triangular number.
*
* @module @stdlib/assert/is-square-triangular-number
*
* @example
* import isSquareTriangularNumber from '@stdlib/assert/is-square-triangular-number';
*
* var bool = isSquareTriangularNumber( 36.0 );
* // returns true
*
* bool = isSquareTriangularNumber( new Number( 36.0 ) );
* // returns true
*
* bool = isSquareTriangularNumber( 3.14 );
* // returns false
*
* bool = isSquareTriangularNumber( -5.0 );
* // returns false
*
* bool = isSquareTriangularNumber( null );
* // returns false
*
* @example
* import { isPrimitive as isSquareTriangularNumber } from '@stdlib/assert/is-square-triangular-number';
*
* var bool = isSquareTriangularNumber( 36.0 );
* // returns true
*
* bool = isSquareTriangularNumber( new Number( 36.0 ) );
* // returns false
*
* @example
* import { isObject as isSquareTriangularNumber } from '@stdlib/assert/is-square-triangular-number';
*
* var bool = isSquareTriangularNumber( 36.0 );
* // returns false
*
* bool = isSquareTriangularNumber( new Number( 36.0 ) );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import isPrimitive from './primitive.js';
import isObject from './object.js';


// MAIN //

setReadOnly( main, 'isPrimitive', isPrimitive );
setReadOnly( main, 'isObject', isObject );


// EXPORTS //

export default main;
